document.addEventListener('DOMContentLoaded', function() {
  // Typing animation for name
  const nameElement = document.querySelector('.name');
  const subtitle = document.querySelector('.intro-subtitle');
  const progressCircle = document.querySelector('.progress-circle');
  
  // Set initial state
  nameElement.style.animation = 'typing 1.5s steps(30, end), blink-caret .75s step-end infinite';
  nameElement.style.borderRight = '2px solid #4ecca3';
  nameElement.style.display = 'inline-block';
  nameElement.style.overflow = 'hidden';
  nameElement.style.whiteSpace = 'nowrap';
  nameElement.style.width = '0';
  
  // Animate name typing
  setTimeout(() => {
    nameElement.style.width = nameElement.textContent.length + 'ch';
  }, 500);
  
  // Show subtitle after name finishes typing
  setTimeout(() => {
    subtitle.style.opacity = '1';
    subtitle.style.transition = 'opacity 1s ease-in-out';
    progressCircle.style.opacity = '1';
  }, 2000);
  
  // Show progress circle and then hide intro
  setTimeout(() => {
    const introSection = document.querySelector('.intro-section');
    const mainContent = document.querySelector('.main-content');
    
    introSection.style.opacity = '0';
    introSection.style.transition = 'opacity 0.5s ease-in-out';
    
    setTimeout(() => {
      introSection.style.display = 'none';
      mainContent.style.display = 'block';
    }, 500);
  }, 4000); // Adjust timing as needed
});